

.works-content {
   position: absolute;
   height: 100vh;
   z-index: 10000;
   //max-width: 1240px;
   width: 500px;
   margin: 0 auto;
   padding: 0 20px;
   padding-top: 50px;
   display: grid;
   //place-items: center;
   //gap: 15px;
   grid-template-columns: 1fr 1fr;
   background-color: #fff;
   //align-items: center;
   //justify-items: center;
   justify-content: center;
   //align-content: center;
   align-content: baseline;
   gap: 20px;
   //grid-auto-flow: column;
   & .buttons {
      padding: 10px 10px;
      width: 170px;
      height: 70px;
      border-radius: 10px;
      background: #FFF;
      box-shadow: 0px 4px 30px 0px rgba(112, 194, 212, 0.45);
      display: grid;
      place-items: center;
      font-family: Alternate Gothic No3 D, serif;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      font-size: 30px;
   }
}

.button-home{
   z-index: 10000;
   position: absolute;
   font-family: Alternate Gothic No3 D, serif;
   font-style: normal;
   font-weight: 400;
   line-height: normal;
   display: grid;
   align-items: center;
   width: min-content;
   grid-template-columns: max-content 1fr;
   margin-top: 10px;
   margin-left: 10px;
   &:hover {
      cursor: pointer;
   }
   & img {
      width: 30px;
      height: 30px;
   }
}
@media only screen and (max-width: 420px) {
   .works-content {
      width: 100%;
   }
}
