

.menu-panel {
   position: absolute;
   z-index: 10000;
   width: 500px;
   height: 100vh;
   background-color: white;
   padding: 40px 20px;
   display: grid;
   place-items: center;
   & .menu-close{
      position: absolute;
      z-index: 10001;
      width: 40px;
      height: 40px;
      background-color: #D9D9D9;
      border-radius: 10px;
      right: 30px;
      top: 30px;
      display: grid;
      place-items: center;
      &:hover {
         cursor: pointer;
         &::before,&::after {
            background-color: white;
         }
      }
      &::before, &::after {
         position: absolute;
         left: 17.5px;
         content: ' ';
         height: 33px;
         width: 2px;
         background-color: #333;
      }
      &::before {
         transform: rotate(45deg);
      }
      &::after {
         transform: rotate(-45deg);
      }
   }
   & .menu-wrapper {
      position: relative;
      display: grid;
      justify-items: center;
      width: 100%;

      & .menu-title {
         color: #000;
         font-size: 65px;
         font-family: Alternate Gothic No3 D, serif;
         font-style: normal;
         font-weight: 600;
         line-height: normal;
         //margin-top: 50px;
         padding-bottom: 50px;
         text-align: center;
      }
      & .menu-description {
         color: #000;
         //text-align: right;
         font-size: 48px;
         font-family: Alternate Gothic No3 D, serif;
         font-style: normal;
         font-weight: 400;
         line-height: normal;
      }
      & .menu-button {
         height: 65px;
         background: #FFF960;
         width: 220px;
         margin: 0;
         display: grid;
         place-items: center;
         font-family: Alternate Gothic No3 D, serif;
         font-style: normal;
         font-weight: 400;
         line-height: normal;
         font-size: 40px;
         transition: all .2s ease-in-out;
         &:hover {
            cursor: pointer;
            font-size: 43px;
            transition: all .2s ease-in-out;
         }
      }
      & .menu-image {
         padding-top: 53px;
         padding-bottom: 63px;
      }
   }
}

.menu-open{
   position: absolute;
   width: 50px;
   height: 50px;
   border-radius: 50%;
   border: 2px solid white;
   z-index: 10000;
   box-sizing: content-box;
   font-family: Alternate Gothic No3 D, serif;
   font-style: normal;
   font-weight: 400;
   line-height: normal;
   font-size: 40px;
   color: white;
   margin-top: 20px;
   margin-left: 20px;
   & .menu-open__wrapper {
      box-sizing: content-box;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      display: grid;
      place-items: center;
      font-family: Alternate Gothic No3 D, serif;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      font-size: 40px;
      color: white;
      transition: all .3s ease-in-out;
   }

   &:hover {
      & .menu-open__wrapper {
         cursor: pointer;
         color: black;
         transition: all .3s ease-in-out;
         width: 53px;
         height: 53px;
         transform: rotate3d(0, 2, 0.0, 3.142rad);
      }
   }
}



@media only screen and (max-width : 420px) {
   .menu-panel {
      width: 100%;
      & .menu-wrapper {
         & .menu-title {
            font-size: 54px;
         }
         & .menu-description {
            font-size: 30px;
         }
      }
   }
}
